.blog-custom-container {
  padding-bottom: 60px;
  .blog-header-text {
    font-family: "Montserrat";
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    letter-spacing: 0em;
    text-align: center;
    padding-top: 60px;
  }
  .blog-read-more {
    margin-top: 70px;
  }
  .blog-title {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 44px;
    color: #db4d29;
    text-align: center;
    margin-bottom: 40px;
    margin-top: 8px;
  }
  .blog-custom-content {
    display: flex;
    height: 564px;
    .blog-content-item-right {
      display: flex;
      flex-direction: column;
      .blog-content-item-card {
        cursor: pointer;
        width: 624px;
        .blog-content-item-img {
          padding: 18px;
          display: flex;
          .blog-content-item-img-style {
            flex-direction: column;
            min-width: 264px;
            min-height: 264px;
            max-width: 264px;
            max-height: 264px;
          }
          .blog-content-item-child {
            flex-direction: column;
            justify-content: center;
            padding-left: 12px;
            .blog-content-item-channel {
              color: #db4d29;
              font-family: Montserrat;
              font-size: 14px;
              font-style: normal;
              font-weight: 600;
              line-height: 20px;
            }
            .blog-content-item-title {
              color: var(--color-customize-text-color, #282828);
              font-family: "Montserrat";
              font-size: 20px;
              font-style: normal;
              font-weight: 700;
              line-height: 1.5em;
              height: 3em;

              display: inline-block;
              display: -webkit-box !important;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              overflow: hidden;
              text-overflow: ellipsis;
              width: 360px;
            }
            .blog-content-item-info {
              .blog-content-item-info-item {
                display: inline-flex;
                .blog-content-item-info-item-by {
                  margin-left: 30px;
                  color: var(--color-default-button-topping-option-selling-price-time-quantity-price, #959595);
                  font-family: "Montserrat";
                  font-size: 14px;
                  font-style: normal;
                  font-weight: 400;
                  line-height: 24px; /* 171.429% */
                  .blog-content-item-info-item-by-name {
                    color: #282828;
                    font-family: "Montserrat";
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 24px;
                  }
                }
                .blog-content-item-info-item-date {
                  margin-left: 8px;
                  color: var(--color-default-button-topping-option-selling-price-time-quantity-price, #959595);
                  font-family: "Montserrat";
                  font-size: 14px;
                  font-style: normal;
                  font-weight: 400;
                  line-height: 24px;
                }
              }
            }
            .blog-content-item-description {
              overflow: hidden;
              color: var(--color-customize-text-color, #282828);
              text-overflow: ellipsis;
              font-family: "Montserrat";
              font-size: 14px;
              font-style: normal;
              font-weight: 400;
              line-height: 20px;

              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box !important;
              -webkit-line-clamp: 3;
              -webkit-box-orient: vertical;
              display: inline-block;
              width: 360px;
            }
          }
        }
      }
    }
    .blog-content-item-left {
      display: flex;
      flex-direction: column;
      .blog-content-item-card {
        cursor: pointer;
        .blog-content-item-img {
          flex-grow: 1;
          padding: 18px;
          .blog-content-item-img-style {
            min-height: 420px;
            min-width: 540px;
            max-width: 540px;
            max-height: 420px;
          }
          .blog-content-item-child {
            flex-direction: column;
            justify-content: center;
            padding-top: 12px;
            .blog-content-item-channel {
              color: #db4d29;
              font-family: "Montserrat";
              font-size: 14px;
              font-style: normal;
              font-weight: 600;
              line-height: 20px;
            }
            .blog-content-item-title {
              color: var(--color-customize-text-color, #282828);
              font-family: "Montserrat";
              font-size: 20px;
              font-style: normal;
              font-weight: 700;
              line-height: 28px; /* 140% */

              display: inline-block;
              white-space: nowrap;
              overflow: hidden;
              width: 540px;
              text-overflow: ellipsis;
            }
            .blog-content-item-info {
              .blog-content-item-info-item {
                display: inline-flex;
                .blog-content-item-info-item-by {
                  margin-left: 30px;
                  color: var(--color-default-button-topping-option-selling-price-time-quantity-price, #959595);
                  font-family: "Montserrat";
                  font-size: 14px;
                  font-style: normal;
                  font-weight: 400;
                  line-height: 24px; /* 171.429% */
                  .blog-content-item-info-item-by-name {
                    color: #282828;
                    font-family: "Montserrat";
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 24px;
                  }
                }
                .blog-content-item-info-item-date {
                  margin-left: 8px;
                  color: var(--color-default-button-topping-option-selling-price-time-quantity-price, #959595);
                  font-family: "Montserrat";
                  font-size: 14px;
                  font-style: normal;
                  font-weight: 400;
                  line-height: 24px;
                }
              }
            }
            .blog-content-item-description {
              overflow: hidden;
              color: var(--color-customize-text-color, #282828);
              text-overflow: ellipsis;
              font-family: "Montserrat";
              font-size: 14px;
              font-style: normal;
              font-weight: 400;
              line-height: 20px;

              display: inline-block;
              display: -webkit-box !important;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              overflow: hidden;
              text-overflow: ellipsis;
              width: 540px;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 1024px) {
  .blog-custom-container {
    .blog-custom-content {
      display: block;
      height: auto;
      .blog-content-item-left {
        .blog-content-item-card {
          cursor: pointer;
          .blog-content-item-img {
            .blog-content-item-img-style {
              max-width: 786px;
              max-height: 420px;
              min-height: 420px;
              min-width: 786px;
            }
            img {
              height: 420px;
            }
            .blog-content-item-child {
              margin-left: 4px;
              .blog-content-item-title {
                width: 780px;
              }
              .blog-content-item-description {
                width: 780px;
                white-space: normal;
              }
            }
          }
        }
      }
      .blog-content-item-right {
        .blog-content-item-card {
          cursor: pointer;
          .blog-content-item-img {
            .blog-content-item-child {
              .blog-content-item-title {
                width: 530px;
              }
              .blog-content-item-description {
                width: 530px;
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 640px) {
  .blog-custom-container {
    .button-arrow-blog {
      .button-left-arrow {
        display: inline-block;
        position: absolute;
        z-index: 2;
        margin-top: 25%;
        left: 0;
      }
      .button-right-arrow {
        display: inline-block;
        position: absolute;
        z-index: 2;
        margin-top: 25%;
        right: 0;
      }
    }
    .blog-content-item-card {
      cursor: pointer;
      img {
        display: block;
        margin: 0 auto !important;
        min-width: 88%;
        min-height: 264px;
        max-width: 88%;
        max-height: 264px;
        margin-left: 2%;
      }
      .blog-content-item-child {
        padding-left: 24px;
        padding-right: 24px;
        .blog-content-item-channel {
          margin-top: 12px;
          margin-bottom: 12px;
          color: #db4d29;
          font-family: "Montserrat";
          font-size: 14px;
          font-style: normal;
          font-weight: 600;
          line-height: 20px;
        }
        .blog-content-item-title {
          .blog-content-item-title-text {
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box !important;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            font-family: "Montserrat";
            font-size: 20px;
            font-style: normal;
            font-weight: 700;
            line-height: 28px;
            width: 338px !important;
            display: block;
          }
        }
        .blog-content-item-info {
          .blog-content-item-info-item {
            .blog-content-item-info-item-by {
              margin-left: 30px;
            }
          }
        }
        .blog-content-item-description {
          margin-top: 12px;
          display: -webkit-box !important;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;

          font-family: "Montserrat";
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          line-height: 20px;
        }
      }
    }
  }
  .blog-read-more {
    margin: 70px auto !important;
  }
}
