.product-detail-theme-pho-viet-section {
  width: 100%;
  height: max-content;
  background: #ffffff;

  #productDetailBody {
    width: 100% !important;
    .product-detail-theme-pho-viet-section-group {
      background: #ffffff;
      display: flex;
      max-width: 100%;
      background-attachment: initial;
      background-repeat: no-repeat !important;
      background-size: 100% 100% !important;
      background-position: center !important;
      margin: auto;
      padding: 10px 0;
      min-width: 1178px;

      .product-detail-theme-pho-viet-section-left {
        width: 50%;
        height: max-content;
        padding: 10px;
        position: relative;
        margin-right: 75px;

        .product-detail-image-swiper {
          width: 100%;
          height: 100%;
          border: 1px solid #e6e6e6;
          border-radius: 20px;
          aspect-ratio: 1/1;

          .swiper-button-prev,
          .swiper-button-next {
            color: #51526c !important;
          }

          .swiper-wrapper {
            .swiper-slide {
              display: flex;
              align-items: center;
              justify-content: center;
              padding: unset !important;
              .image-zoom-in {
                width: 50% !important;
                height: 50% !important;
                position: unset !important;
              }
            }
          }
        }

        .slider-product-detail {
          width: 100%;
          height: 570px;
          border: 1px solid #e6e6e6;
          border-radius: 20px;
          display: inline-flex !important;
          align-items: center;
          justify-content: center;
          .product-detail-slider-image {
            width: 100%;
            display: inline-flex !important;
            align-items: center;
            justify-content: center;
            height: 100%;
            .image-product-detail {
              width: 386px;
              height: 379px;
            }
          }
          .slick-arrow {
            width: 18.75px;
            height: 37.5px;
            z-index: 9999;
          }
          .slick-next {
            right: 4%;
          }
          .slick-prev {
            left: 4%;
          }
        }

        .swiper-small-image {
          .swiper-wrapper {
            .swiper-slide {
              width: 130px !important;
              height: 130px;
              display: flex;
              align-items: center;
              justify-content: center;
              border-radius: 20px;
              border: 1px solid #e6e6e6;

              .image-zoom-out {
                width: 113px;
                height: 113px;
                border-radius: 20px;
              }
            }

            .swiper-slide-thumb-active {
              border: 1px solid #db4d29;
            }
          }
        }

        .discount-product-detail {
          position: absolute;
          top: 26px;
          left: 26px;
          max-width: max-content;
          max-height: max-content;
          font-weight: 500;
          background: #ffa318;
          border-radius: 49px;
          color: #fff;
          min-width: 64px;
          text-align: center;
          min-height: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 9;

          .discount-text {
            margin: 6.02px 12px;
          }
        }

        .mySwiper {
          width: 100%;
        }

        > .ant-image {
          border: 1px solid #e6e6e6;
          padding: 16px;
        }

        .ant-image,
        .ant-image-error {
          width: 100%;
          height: 100%;
          aspect-ratio: 1/1;
          border-radius: 20px;

          .product-image {
            width: 100%;
            height: 100%;
            aspect-ratio: 1/1;
            border-radius: 20px;
          }
        }

        .image-sub-group {
          margin-top: 24px;
          .ant-image {
            height: 130px;
            .image-sub {
              width: 130px;
              height: 130px;
              border-radius: 20px;
              border: 1px solid #db4d29;
            }
          }
        }
      }

      .product-detail-theme-pho-viet-section-right {
        width: 50%;
        height: 100%;

        .product-detail-theme-pho-viet-section-right-content {
          .product-name {
            width: 100%;
            font-size: 36px;
            font-weight: 700;
            margin: unset;
          }

          .group-star-rate {
            display: flex;
            align-items: baseline;
            .total-review {
              margin-left: 12px;
              font-size: 14px;
              font-weight: 500;
              text-transform: uppercase;
            }
          }

          .description-product {
            font-size: 14px;
            margin-top: 25.5px;
            text-align: justify;
            color: #959595;
          }

          .product-price {
            margin-top: 20px;
            display: flex;
            align-items: center;
            .product-price-left {
              width: max-content;
              white-space: nowrap;
              .product-price-discount {
                margin-left: 12px;
                font-size: 24px;
                font-weight: 700;
                color: #db4d29;
              }
              .product-price-original {
                font-size: 18px;
                font-weight: 500;
                color: #959595;
                text-decoration: line-through;
              }
            }
            .product-price-right {
              display: flex;
              justify-content: flex-end;
              align-items: center;
              width: 100%;
              padding: 0 10px;
              .product-price-btn-increase,
              .product-price-btn-decrease {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                font-size: 20px;
                font-weight: bold;
                cursor: pointer;
                align-items: center;
              }

              .product-price-btn-decrease {
                background: #ebebeb;
                color: #51526c;
              }
              .product-price-btn-increase {
                background: #db4d29;
                color: #fff;
              }

              .product-price-quantity {
                margin: 0 46.5px;
              }
            }
          }

          .product-description {
            color: #959595;
            font-size: 14px;
            margin-top: 20px;
            text-align: justify;

            .ant-typography {
              .ant-typography-expand {
                color: #db4d29;
                font-size: 14px;
              }
            }
          }

          .group-multiple-price {
            margin-top: 24px;
            width: 100%;
            .group-collapse {
              border: unset;
              border-radius: 8px;

              .ant-collapse-item {
                border: unset;
                .ant-collapse-header {
                  background: #f9f9f9;
                  font-size: 20px;
                  font-weight: 700;
                  border-radius: 8px;
                  text-transform: uppercase;
                }
                .ant-collapse-content {
                  border: unset;
                  .ant-collapse-content-box {
                    padding: unset !important;

                    .group-multiple-price-content {
                      display: grid;
                      align-items: center;

                      .group-multiple-price-item {
                        height: fit-content;
                        width: 100%;
                        display: inline-flex;
                        align-items: center;
                        justify-content: space-between;
                        padding: unset;
                        border-bottom: 1px solid #e6e6e6;

                        .price-name {
                          height: 100%;
                          display: grid;
                          align-items: center;
                          padding: 8px 0;
                          margin-left: 16px;

                          .name {
                            font-size: 16px;
                            font-weight: 500;
                          }

                          .price-name-value {
                            font-size: 14px;
                            font-weight: 400;
                          }
                        }

                        .checkbox-price {
                          margin-right: 16px;
                        }

                        .price-name-value-radio {
                          width: 24px;
                          height: 24px;
                        }

                        .ant-radio-wrapper {
                          .ant-radio {
                            .ant-radio-inner {
                              border: 1px solid #a5abde;
                            }
                          }

                          .ant-radio-checked {
                            .ant-radio-inner {
                              border: 1px solid #ff8c21;
                            }
                          }
                        }
                      }
                    }

                    .group-multiple-price-content *:last-child {
                      border: unset;
                    }
                  }
                }
              }
            }
          }

          .group-product-option {
            width: 100%;
            margin-top: 12px;

            .group-collapse {
              border: unset;
              border-radius: 8px;

              .ant-collapse-item {
                border: unset;
                .ant-collapse-header {
                  background: #f9f9f9;
                  font-size: 20px;
                  font-weight: 700;
                  border-radius: 8px;
                  text-transform: uppercase;
                }
                .ant-collapse-content {
                  border: unset;
                  .ant-collapse-content-box {
                    padding: unset !important;

                    .group-product-option-content {
                      display: grid;
                      align-items: center;
                      padding: 0 16px;
                      justify-content: stretch;

                      .group-product-option-item {
                        height: fit-content;
                        width: 100%;
                        display: inline-flex;
                        align-items: center;
                        padding: unset;
                        border-bottom: 1px solid #e6e6e6;
                        justify-content: space-between;

                        .option-name {
                          font-size: 16px;
                          font-weight: 500;
                          width: auto;
                        }

                        .option-level-value {
                          width: fit-content;
                          display: flex;
                          align-items: center;
                          flex-wrap: wrap;
                          justify-content: flex-end;
                          margin: 8px;
                          gap: 10px;

                          .option-level-value-item {
                            height: 32px;
                            border-radius: 7px;
                            background: #fff;
                            color: #000;
                            min-width: 82px;
                            font-size: 12px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            font-weight: 500;
                            border: unset;
                            white-space: nowrap;

                            .ant-radio-button-checked {
                              background-color: #db4d29;
                              border-radius: 7.5px;
                              border: 1px solid #db4d29;

                              .ant-radio-button-input {
                                width: 0.5px;
                                height: 0.5px;
                                opacity: unset;
                              }

                              .ant-radio-button-input::after {
                                content: "";
                                width: 8px;
                                height: 8px;
                                background: #fff;
                                position: absolute;
                                top: 10px;
                                left: 3px;
                                border-radius: 100%;
                                transition: all 0.2s ease;
                              }
                            }

                            .ant-radio-button:last-child {
                              border: unset;
                            }
                          }

                          .ant-radio-button-wrapper-checked {
                            color: #fff;
                            font-size: 14px;
                          }

                          .option-level-value-item:not(:first-child)::before {
                            background-color: unset !important;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }

          .group-product-topping {
            margin-top: 12px;
            width: 100%;

            .group-collapse {
              border: unset;
              border-radius: 8px;

              .ant-collapse-item {
                border: unset;
                .ant-collapse-header {
                  background: #f9f9f9;
                  font-size: 20px;
                  font-weight: 700;
                  border-radius: 8px;
                  text-transform: uppercase;
                }
                .ant-collapse-content {
                  border: unset;
                  .ant-collapse-content-box {
                    padding: unset !important;

                    .group-product-topping-content {
                      display: grid;
                      align-items: center;

                      .group-product-topping-item {
                        height: 60px;
                        width: 100%;
                        display: inline-flex;
                        align-items: center;
                        justify-content: space-between;
                        padding: unset;
                        border-bottom: 1px solid #e6e6e6;
                        gap: 10px;

                        .topping-name {
                          height: 100%;
                          display: grid;
                          align-items: center;
                          padding: 8px 0;
                          margin-left: 16px;

                          .name {
                            font-size: 16px;
                            font-weight: 500;
                          }

                          .topping-price-value {
                            font-size: 14px;
                            font-weight: 400;
                          }
                          .topping-original-price-value {
                            color: #959595;
                            font-family: "Montserrat";
                            font-style: normal;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 20px;
                            text-decoration-line: line-through;
                            padding-right: 12px;
                          }
                        }

                        .topping-change-quantity {
                          display: inline-flex;
                          align-items: center;
                          justify-content: end;
                          margin-right: 16px;

                          .topping-quantity-btn-increase,
                          .topping-quantity-btn-decrease {
                            width: 24px;
                            height: 24px;
                            border-radius: 50%;
                            display: flex;
                            align-items: self-end;
                            justify-content: center;
                            font-weight: bold;
                            cursor: pointer;
                            align-items: center;
                          }

                          .topping-quantity-btn-decrease {
                            background: #f9f1df;
                            color: #51526c;
                          }

                          .topping-quantity-btn-increase {
                            background: #db4d29;
                            color: #fff;
                          }

                          .topping-quantity-value {
                            margin: 0 20px;
                            font-size: 18px;
                          }
                        }
                      }
                    }

                    .group-product-topping-content *:last-child {
                      border: unset;
                    }
                  }
                }
              }
            }
          }

          .input-note-product-detail {
            position: relative;
            .icon-note-product-detail {
              position: absolute;
              bottom: 25%;
              left: 2.5%;
            }
            .product-note {
              width: 100%;
              min-height: 56px;
              color: #282828;
              border-radius: 8px;
              font-size: 16px;
              line-height: 24px;
              font-weight: 500;
              padding: 20px 16px 20px 50px;
              border: 1px solid #e6e6e6;
            }
            .product-note:focus {
              border: 1px solid #e6e6e6;
            }
            .note-icon {
              position: relative;
              top: 48px;
              left: 10px;
              margin-left: 5px;
              z-index: 998;
            }
          }

          .btn-add-to-cart {
            width: 100%;
            height: 60px;
            background: #db4d29;
            margin-top: 24px;
            border-radius: 12px;
            color: #ffffff;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: unset;
            font-size: 20px;
            font-weight: 500;

            .btn-add-to-cart-text {
              display: flex;
              align-items: center;

              .icon-check-out {
                margin-left: 10px;
              }
            }

            .btn-add-to-cart-price-value {
              margin-left: 20px;
            }

            .btn-add-to-cart-text {
              margin-right: 20px;
            }
          }

          .btn-add-to-cart:hover {
            cursor: pointer;
            border: 1px solid #db4d29;
            font-weight: 500;
          }

          .margin-top {
            margin-top: 12px !important;
          }

          .group-product-combo {
            margin-top: 24px;
            width: 100%;

            .group-collapse {
              border: unset;
              border-radius: 8px;

              .product-name-banner {
                border: unset;

                .ant-collapse-header {
                  background: #f9f1df;
                  font-size: 16px;
                  font-weight: bold;
                  color: #000000;
                  border-radius: 8px;

                  .ant-collapse-header-text {
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                  }
                }

                .ant-collapse-content {
                  border: unset;

                  .ant-collapse-content-box {
                    padding: 12px 0 0 0 !important;
                    .group-product-option,
                    .group-product-topping {
                      margin-top: unset;

                      .group-collapse {
                        .ant-collapse-item {
                          .ant-collapse-header {
                            background: #f9f9f9;
                            color: #959595;
                          }

                          .ant-collapse-content {
                            .ant-collapse-content-box {
                              padding-top: unset !important;
                            }
                          }
                        }
                      }
                    }

                    .group-product-topping {
                      margin-top: 12px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .chatAboutProduct {
    width: 100% !important;
  }
  .product-detail-theme-pho-viet-section-reviews {
    height: 300px;
    border: 1px solid #f9f1df;
    margin: 0px 40px !important;
    border-radius: 10px;
    margin-top: 10px;

    .product-detail-reviews-title {
      width: 100%;
      height: 50px;
      background: #db4d29;
      display: flex;
      align-items: center;
      padding: unset !important;
      border-radius: 12px 12px 0px 0px;

      .product-detail-reviews-title-content {
        width: 100%;
        padding: 0 15px;
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 28px;
        color: #ffffff;
      }
    }
    .product-detail-reviews-content {
      height: calc(100% - 50px);
      width: 100%;
      .product-detail-reviews-no-content {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        flex-direction: column;
      }
      .product-detail-reviews-no-content-text {
        font-size: 14px;
        font-weight: 500;
        margin-bottom: unset !important;
      }
    }
  }

  #relatedProductsDetail {
    .related-products-container {
      justify-content: center;
      align-items: center;
      display: flex;
      background-attachment: initial;
      background-repeat: no-repeat !important;
      background-size: cover !important;
      background-position: center !important;

      .related-products {
        margin-bottom: 60px;
        font-style: normal;

        .top-content-product {
          align-items: center;
          justify-content: center;
          height: 104px;
          display: flex;

          .top-tile {
            font-weight: 700;
            font-size: 40px;
            line-height: 44px;
            display: flex;
            align-items: center;
            text-align: center;
            color: #db4d29;
            label {
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
              overflow: hidden;
            }
          }
        }

        .middle-content-product-detail {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          .product-row {
            display: flex;
            .product-box {
              width: 264px;
              height: 415px;
              border: 1px solid #e6e6e6;
              border-radius: 20px;
              color: #db4d29;
              margin-left: 22px;
              margin-right: 22px;
              background-color: #ffffff;
              .discount-box-top {
                margin-top: 12px;
                margin-left: 12px;
                height: 33px;

                .discount-box {
                  display: flex;
                  flex-direction: row;
                  align-items: flex-start;
                  padding: 4px 8px;
                  gap: 10px;
                  width: 39px;
                  height: 24px;
                  background: #db4d29;
                  border-radius: 30px;

                  label {
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 16px;
                    color: #ffffff;
                  }
                }
              }

              .thumnail {
                margin: 0px 32px 0px 32px;
                width: 200px;
                height: 196.65px;
              }

              .product-content {
                margin-left: 12px;
                margin-right: 12px;

                .rate {
                  display: flex;
                  align-items: center;
                  padding-bottom: 8px;
                }

                .name {
                  margin-top: 8px;
                  font-weight: 700;
                  font-size: 18px;
                  line-height: 22px;
                  align-items: center;
                  text-transform: capitalize;
                  color: #000000;
                }

                .description {
                  margin-top: 8px;
                  font-weight: 500;
                  font-size: 12px;
                  line-height: 16px;
                  align-items: center;
                  color: #959595;
                }

                .bottom-content {
                  margin-top: 5px;
                  height: 36px;
                  width: 100%;
                  height: 36px;
                  display: flex;

                  .price-box-left {
                    width: 200px;
                    align-items: center;
                    display: flex;

                    .original-price-related {
                      font-weight: 500;
                      font-size: 13px;
                      line-height: 16px;
                      margin-right: 12px;
                      text-decoration-line: line-through;
                      color: #959595;
                    }

                    .price {
                      font-weight: 700;
                      font-size: 15px;
                      line-height: 18px;
                      color: #db4d29;
                    }
                  }

                  .delete {
                    float: inline-end;
                  }

                  .icon {
                    cursor: pointer;
                  }
                }
              }
            }

            .product-main-theme2 {
              margin-right: 43px;

              .discount-box {
                left: -5%;
              }
            }

            .product-main-theme2 {
              margin-right: 43px;

              .discount-box {
                left: -5%;
                display: flex;
                align-items: center;
                justify-content: center;
                text-decoration: none;
                min-width: 39px;
                height: 24px;
                white-space: nowrap;
                max-width: 100%;
                background: #ffa318;
              }
            }
          }
        }
      }
    }
  }
}

.related-product-detail-title {
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 21px;
  color: #282828;
}

#productDetailHeader {
  height: 323px;
  width: 100%;
  text-align: center;

  .product-detail-top-img {
    position: relative;
    height: 100%;
    width: 100%;
    background-attachment: initial;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    background-position: center !important;

    .title-product {
      position: absolute;
      font-size: 40px;
      font-weight: 700;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;

      .span-title-product {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        width: 90%;
      }
    }
  }
}

.detail-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.w-50 {
  max-width: 100%;
  min-width: 1178px;
}

.w-100 {
  width: 100%;
}

.display-none {
  display: none !important;
}

@media (max-width: 576px) {
  .product-detail-theme-pho-viet-section {
    width: 100vw;
    #productDetailBody {
      .product-detail-theme-pho-viet-section-group {
        display: grid;
        min-width: 100vw;
        max-width: 100vw;

        .product-detail-theme-pho-viet-section-left {
          padding: unset;
          height: max-content;
          width: 100vw;
          margin: unset;

          > .ant-image {
            border: 1px solid #e6e6e6;
            padding: 10px;
            width: calc(100vw - 32px);
            margin: auto;
          }

          .ant-image {
            display: flex;
            align-items: center;
            justify-content: center;

            .product-image {
              width: calc(100vw - 20px);
            }
          }

          .discount-product-detail {
            top: 10px;
          }

          .swiper {
            width: calc(100vw - 32px);
          }

          .image-sub-group {
            margin-top: 22px;
            display: flex;
            align-items: center;
            float: left;
            padding: 0 16px;
            .ant-image {
              height: 108px;
              .image-sub {
                width: 108px;
                height: 108px;
                border-radius: 12px;
              }
