.product-item-card {
  .ant-card-body {
    padding: unset;
    border-radius: unset;
  }
}
.product-main-theme2,
.checkout-related-product-item-theme2 {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 12px;
  width: 273px;
  background: #ffffff;
  border: 1px solid #e6e6e6;
  border-radius: 20px;
  margin-right: 1em;
  &:last-child {
    margin-right: 0;
  }
  .product-image {
    height: 240px;
    width: 240px;
    -moz-background-size: 100% 100%;
    -o-background-size: 100% 100%;
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    text-align: center;
    margin: auto;
    border-radius: 20px;
    position: relative;
    .discount-box {
      padding: 4px 8px;
      position: relative;
      width: fit-content;
      height: 24px;
      left: 0px;
      top: 0px;
      background: #ffa318;
      border-radius: 30px;
      .discount-text {
        width: fit-content;
        height: 16px;
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        display: flex;
        align-items: center;
        color: #ffffff;
        flex: none;
        order: 0;
        flex-grow: 0;
      }
    }
  }
  .product-flash-sale {
    position: absolute;
    top: -12px;
    margin-left: -12px;
    border: 6px solid #c41b0e;
    width: calc(100% + 24px);
    height: calc(100% + 12px);
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    border-bottom: 42px solid #c41b0e;
    .flash-sale-tag {
      position: absolute;
      bottom: -36px;
      left: 2px;
    }
  }
  .product-img {
    width: 249px;
    height: 249px;
    &:hover {
      cursor: pointer;
    }
  }
  .product-rating {
    display: flex;
    height: 24px;
    margin-top: 25.75px;
    align-items: center;
  }
  .product-rating img {
    width: 24px;
    height: 24px;
  }
  .product-name {
    width: 240px;
    height: 22px;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: #000000;
    margin-top: 8px;
    text-transform: capitalize;
    display: -webkit-box !important;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    &:hover {
      cursor: pointer;
    }
  }
  .product-description {
    width: 240px;
    height: 46px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 23px;
    align-items: center;
    justify-content: center;
    color: #959595;
    margin-top: 8px;
    display: -webkit-box !important;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  .price-box {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 8px;
    height: 48px;
    .price-box-left {
      display: flex;
      flex-direction: row;
      align-items: center;
      .product-price {
        width: fit-content !important;
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 16px;
        text-decoration-line: line-through;
        color: #959595;
        user-select: none;
        margin-right: 10px;
      }
      .product-price-with-discount {
        width: fit-content !important;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        color: #db4d29;
        line-height: 24px;
        border: solid 1px transparent;
        background: none !important;
      }
    }
    .cart {
      width: 48px;
      height: 48px;
      border-radius: 30px;
      cursor: pointer;
      background: #db4d29;
      border-radius: 30px;
      padding: 12px;
    }
  }

  .hyper-link {
    text-decoration: none;
  }
}

@media screen and (max-width: 639px) {
  .product-main-theme2,
  .checkout-related-product-item-theme2 {
    margin-right: 0 !important;
    margin-left: 16px !important;
    width: calc(100% / 2 - 27px);
    margin-top: 24px;
    .product-image {
      width: 140px !important;
      height: 140px !important;
      margin-left: 0 !important;
      align-items: center;
      margin-top: 0 !important;
    }
    .product-name {
      width: 100%;
      height: 22px;
      .item {
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        overflow-x: hidden;
      }
    }
    .product-description {
      width: 100%;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      align-items: center;
      color: #959595;
    }
    .price-box {
      .price-box-left {
        flex-direction: row !important;
        justify-content: space-between !important;
        .product-price {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          align-items: center;
          text-decoration-line: line-through;
          color: #959595;
          margin-top: 0 !important;
          margin-right: 12px;
        }
        .product-price-with-discount {
          font-style: normal;
          font-weight: 600;
          font-size: 16px;
          line-height: 24px;
          align-items: center;
          margin-top: 0 !important;
        }
      }
    }

    .hyper-link {
      text-decoration: none;
    }
  }
  .checkout-related-product-item-theme2 {
    width: 100% !important;
  }
}

@media (max-width: 575px) {
  .product-main-theme2 {
    display: flex;
    justify-content: center;
    align-items: center;
    .product-img {
      width: 200px;
      margin: auto;
      height: auto !important;
    }
    .price-box {
      .price-box-left {
        flex-direction: column !important;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .product-main-theme2 {
    .price-box {
      .price-box-left {
        .product-price {
          font-size: 12px;
        }
        .product-price-with-discount {
          font-size: 14px;
        }
      }
      .cart-item {
        height: 40px;
        width: 40px;
      }
      .cart {
        height: 40px;
        width: 40px;
      }
    }
  }
}
