$menu_paddding: 25px;
.product-list-detail-theme2-customize,
.product-list-detail-theme2 {
  .empty-product-list {
    .empty-product-list-text {
      padding-top: 22px;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      display: flex;
      align-items: center;
      color: #282828;
    }
    height: 762px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .nav-category-sticky {
    position: fixed;
    top: 74px;
    width: 100%;
    z-index: 100;
    min-width: unset !important;
    max-width: unset !important;
  }

  width: 100%;
  margin: auto;
  background: #ffffff;
  .product-menu {
    min-width: 60%;
    max-width: 80%;
    height: 60px;
    margin: auto;
    border-radius: 40px;
    border: 1px solid #e6e6e6;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    text-align: center;
    background: white !important;
    .li-selected {
      background: #db4d29;
      border-radius: 40px;

      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #ffffff;
      padding-left: $menu_paddding;
      padding-right: $menu_paddding;
      padding-top: 16px;
      padding-bottom: 16px;
      cursor: pointer;
      user-select: none;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
    .li-normal {
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #282828;
      cursor: pointer;
      user-select: none;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      padding-top: 16px;
      padding-bottom: 16px;
      &:last-child {
        padding-right: $menu_paddding;
      }
      &:first-child {
        padding-left: $menu_paddding;
      }
    }
    .ul-product-list-popover {
      margin-top: 15px;
      padding: 12px;
      max-height: 644px;
      overflow: auto;
    }
    .arrow-down-popover {
      margin-top: 18px;
      margin-right: 15px;
    }
    .li-selected-popover {
      background: #db4d29;
      border-radius: 12px;

      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #ffffff;
      padding-top: 16px;
      padding-bottom: 16px;
      padding-left: $menu_paddding;
      padding-right: $menu_paddding;
      cursor: pointer;
      user-select: none;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      background-image: url(../../../assets/icons/checked-icon.svg);
      background-repeat: no-repeat;
      background-position: right;
      background-position-x: calc(100% - 15px);
    }
    .li-normal-popover {
      padding-top: 16px;
      padding-bottom: 16px;

      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #282828;
      cursor: pointer;
      user-select: none;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
  }
  .product-category {
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    display: flex;
    align-items: center;
    text-transform: capitalize;
    color: #000000;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 50px;
    cursor: pointer;
    user-select: none;
    scroll-margin-top: 130px;
  }
  .product-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    gap: 20px;
    padding-left: 50px;
  }
}
.product-list-detail-theme2-customize:active {
  border: 3px solid blue;
}

.product-list-detail-theme2 {
  .product-menu {
    max-width: 62%;
  }
  .list-product-by-category {
    max-width: 1290px;
    width: 100%;
    margin: 0 auto;
  }
}

@media screen and (max-width: 639px) {
  .product-list-detail-theme2 {
    .nav-category-sticky {
      top: 0;
    }

    .product-menu {
      min-width: 100%;
      max-width: 100%;
      height: 48px;
      padding-left: 5px;
      padding-right: 5px;
      .li-selected {
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        margin-left: 12px;
        align-items: center;
        text-overflow: unset;
        overflow: unset;

        &:first-child {
          margin-left: -4px !important;
        }
      }
      .li-normal {
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        align-items: center;
        color: #282828;
        margin-left: 4px;
        text-overflow: unset;
      }
      .ant-popover-placement-bottom,
      .ant-popover-placement-top {
        left: 10px !important;
        width: calc(100% - 16px);
      }
      .ul-product-list-popover {
        margin-top: 12px;
        max-width: 398px;
        width: 100%;
        height: auto;
      }
      .arrow-down-popover {
        margin-top: 13px;
        margin-right: 10px;
        margin-left: 16px;
      }
    }
    .product-category {
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      align-items: center;
      color: #000000;
      padding-left: 5px;
      scroll-margin-top: 46px;
    }
    .product-list {
      gap: 5px !important;
      padding-left: 0;
    }
  }
}
