.banner-menu-special-swiper {
  .swiper-slide {
    border: unset;
    height: auto;
  }
  .swiper-initialized {
    padding-right: 10px;
  }
  .product-main-theme2 {
    .product-name,
    .product-description {
      text-align: left;
    }
  }
}

.banner-menu-special-swiper .swiper-initialized {
  padding-right: 12px;
  padding-left: 12px;
}

@media screen and (max-width: 639px) {
  .banner-menu-special-swiper {
    .product-main-theme2 {
      width: 100%;
      margin-left: 0 !important;
    }
  }
}
