.post-category-theme2 {
  margin-top: 56px;
  margin-bottom: 56px;
  flex-direction: column;
  background-color: #fff;
  border-radius: 20px;
  padding: 10px 24px;
  .title {
    color: #db4d29;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    text-transform: uppercase;
  }
  .category-list {
    margin-top: 24px;
    width: 368px;

    .category {
      overflow: hidden;
      color: #282828;
      text-overflow: ellipsis;
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: 24px;
      // margin: 16px 0px;

      .category-item {
        margin-bottom: 16px;
        cursor: pointer;
        font-size: 16px;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        line-clamp: 1;
        -webkit-box-orient: vertical;
      }

      .category-item-default {
        color: #db4d29;
      }
      .active {
        color: #db4d29;
      }
    }
  }
}
