.btn-select-discount-code {
  width: 100%;
  height: 56px;
  background: #ffa3181a;
  border: 1px solid #FFA318;
  border-radius: 12px;
  margin-bottom: 24px;
  padding: 16px 5px 16px 16px;
  cursor: pointer;

  .title {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #282828;
    width: calc(100% - 48px);
    float: left;
    padding: 0 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .suffix-icon {
    width: 24px;
    padding: 3px 0px 3px 0px;
  }
  .prefix-icon {
    width: 24px;
  }
}
