.store-branch-address-selector {
  width: 800px !important;
  max-height: 761px;
  border-radius: 12px;
  margin-top: 38px;

  .ant-modal-content {
    padding: 0;
    background-color: white;
    height: 761px;
    border-radius: 12px;
  }
  .ant-modal-header {
    height: 72px;
    background: #db4d29;
    border-radius: 12px 12px 0px 0px;
    margin: 0;
    display: flex;

    .ant-modal-title {
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
      color: #ffffff !important;
      text-transform: unset !important;
      margin: auto;
      display: flex;

      span,
      svg {
        margin: auto;
      }

      span {
        margin-left: 15px;
      }
    }
  }
  .ant-modal-body {
    max-height: 665px;
    padding: 24px 40px 24px 40px;
    background-color: #ffffff;
    border-radius: 0 0 12px 12px;
    .ant-radio-group {
      width: 100%;
    }

    //add scrool
    height: 665px;
    overflow: auto;
  }

  .ant-modal-footer {
    display: none;
  }
  .store-branch-addresses {
    .ant-radio-wrapper:hover .ant-radio-inner {
      border: 1px solid #dddddd;
    }
    .ant-radio-wrapper {
      display: block;
      margin: 0;
      margin-bottom: 24px;
      width: 100%;
      height: 152px;
      padding: 24px;
      background: rgba(255, 255, 255, 0.7);
      border: 1px solid #e2e2e2;
      border-radius: 12px;

      .ant-radio {
        float: right;
        .ant-radio-inner {
          height: 24px;
          width: 24px;
        }
      }

      span:last-child {
        display: flex;
      }
    }
    .ant-radio-wrapper-checked {
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

      .ant-radio {
        .ant-radio-inner {
          border-color: #db4d29;
          background-color: unset;
        }
        .ant-radio-inner::after {
          background-color: #db4d29;
          width: 40px;
          height: 40px;
          border-radius: 40px;
          inset-block-start: -1px;
          inset-inline-start: -1px;
        }
      }
    }
    .store-branch-address-item {
      display: flex;
    }
  }

  .store-branch-address-item {
    width: 100%;
    .d-flex {
      display: flex;
    }
    .address-detail {
      margin-left: 20px;

      font-style: normal;

      .address-label {
        font-weight: 600;
        font-size: 24px;
        line-height: 32px;
        display: flex;
        align-items: center;
        color: #282828;
        margin: 0;
      }

      .distance {
        font-weight: 500;
        font-size: 18px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #db4d29;
        margin-top: 8px;
      }
      .address {
        font-weight: 500;
        font-size: 18px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #282828;

        width: 548px;
        margin: 0;
      }
    }
    .address-icon {
      display: flex;
      .outline {
        width: 36px;
        height: 36px;
        position: absolute;
        border-radius: 50px;
        background: #c53d1a;

        left: 5px;
        top: 5px;
      }
      .address-index {
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #ffffff;

        margin: auto;
      }
    }
  }

  ::-webkit-scrollbar {
    width: 7px !important;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #e0dede;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #db4d29;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #f35027;
  }
}

@media screen and (max-width: 600px) {
  .store-branch-address-selector {
    width: 400px !important;

    .ant-modal-content {
      padding: 0;
      width: 394px;
      height: 737px;
      background-color: white;
    }
    .ant-modal-header {
      .ant-modal-title {
        span {
          font-weight: 600;
          font-size: 24px;
          line-height: 29px;
        }
      }
    }

    .ant-modal-body {
      padding: 24px 16px 0px 16px;
    }

    .store-branch-address-item {
      .address-detail {
        width: 212px;
        .address-label {
          font-weight: 600;
          font-size: 24px;
          line-height: 32px;
        }

        .address-label,
        .address {
          width: 100%;
        }
      }
    }

    .store-branch-addresses {
      .ant-radio-wrapper {
        height: 216px;
        margin-bottom: 24px;
        padding-left: 10px;
        padding-right: 16px;
        padding-top: 16px;
        padding-bottom: 0;
      }
    }

    //height: 244px;

    ::-webkit-scrollbar {
      display: none;
    }
  }
}
