.footer-customize {
  .footer-bg {
    background-size: cover !important;
    height: 100%;
  }
  .store-info {
    display: flex;
    flex-direction: column;
  }
  .store-linked {
    display: flex;
    flex-direction: column;
    width: unset;
  }
  .store-contact-flex {
    display: grid;
    grid-auto-flow: column;
    grid-template-rows: repeat(6, auto);
  }
  .store-contact-flex .body-text-2 {
    overflow: hidden;
    display: -webkit-box !important;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
  }
  .footer-info {
    justify-content: center;
    padding-top: 32px;
    padding-bottom: 25px;
    margin-bottom: 20px;
    border-bottom: 1px solid #4d4d4d;
    grid-template-columns: calc(30% - 100px) 24% 16% 25% !important;
    display: grid;
    gap: 50px;
  }
  .copyright {
    padding: 10px 0;
  }
  .store-contact-flex {
    a {
      text-decoration: none;
    }
  }
  @media (max-width: 800px) {
    .footer-info {
      display: flex;
      flex-direction: column;
      margin: 0 4%;
    }
    .group-footer-info-mobile {
      display: flex;
      flex-direction: row;
      gap: 50px;
    }
    .store-info {
      width: unset;
    }
    .container-logo {
      display: flex;
      justify-content: center;
    }
    .store-info-flex,
    .body-text-2 {
      font-size: 18px;
    }
  }
}
