$white-color: #ffffff;
$primary-color: #db4d29;
$border-radius: 20px;
$green-color: #026f30;

.fnb-promotion-card {
  height: 100%;
  min-width: 397px;
  min-height: 166px;
  width: 358px;
  height: 166px;

  font-style: normal;
  color: $white-color;
  padding: 0px 39px 0px 0px;
  .header-card {
    display: flex;
    .card-title {
      width: 100%;
      display: flex;
      align-items: center;
      padding: 12px;
      background-color: $primary-color;
      border-top-right-radius: $border-radius;
      border-top-left-radius: $border-radius;
      font-weight: 700;
      font-size: 16px;
      line-height: 24px;
      position: relative;
      overflow: hidden;
      text-overflow: ellipsis;
      p {
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;  
        overflow: hidden;
      }
    }
    .card-icon {
      height: 100%;
      width: 40%;
      overflow: hidden;
      position: relative;
      .card-icon-content {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 12px 8px;
        height: 100%;
        width: 100%;
        border-bottom-left-radius: $border-radius;
        .btn-redeem {
          border: 1px solid $primary-color;
          width: 84px;
          height: 40px;
          background: $white-color;
          border-radius: 50px;
          font-weight: 600;
          font-size: 14px;
          color: $primary-color;
          cursor: pointer;
        }
      }
    }
    .card-icon:before {
      content: "";
      display: block;
      width: 100%;
      height: 150%;
      position: absolute;
      border-radius: 26px;
      bottom: 0;
      left: 0;
      box-shadow: -50px 50px 0 0 #db4d29;
      pointer-events: none;
    }
  }
  .discount-code {
    background-color: $primary-color;
    padding-left: 12px;
    border-top-right-radius: $border-radius;
    padding-top: 4px;
    position: relative;
    .discount {
      display: flex;
      align-items: center;
      width: 100%;
      max-width: 241px;
      height: 24px;
      background: #fdf6f4;
      border-radius: 12px;
      color: $primary-color;
      font-weight: 700;
      font-size: 14px;
      padding: 0px 8px;
      .icon {
        display: flex;
        align-items: center;
      }
      .code {
        margin-left: 8px;
      }
    }
  }
  .content-card {
    padding: 0px 26px;
    background-color: $primary-color;
    border-top-right-radius: $border-radius;
    border-bottom-right-radius: $border-radius;
    border-bottom-left-radius: $border-radius;
    height: 96px;
    ul li {
      font-weight: 400;
      font-size: 13px;
      line-height: 20px;
      font-family: "Montserrat";
    }
    p {
      padding-top: 10px;
      margin-bottom: 0px;
      padding-bottom: 12px;
      transform: translateX(-12px);
      font-weight: 400;
      font-size: 12px;
      line-height: 20px;
      letter-spacing: 0.3px;
    }
    .discount-amount{
      border: 1px solid $white-color;
      border-radius: 4px;
      width: fit-content;
      padding: 2px 12px 2px 12px;
      margin-top: 3px;
      font-family: Montserrat;
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: 16px;
    }
    .discount-type{
      margin-left: 20px;
    }
    .end-date{
      position: relative;
      top: -3px;
    }
  }
  .content-card-discount {
    height: 76px !important;
    border-top-right-radius: 0px;
    padding: 0px 26px;
    .end-date {
      p {
        padding: 6px 0px;
      }
    }
    .discount-type{
      margin-left: 20px;
    }
    .discount-amount{
      border: 1px solid $white-color;
      border-radius: 4px;
      width: fit-content;
      padding: 2px 12px 2px 12px;
      margin-top: 5px;
      font-family: Montserrat;
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: 16px;
    }
    .fnb-promotion-card .content-card ul li {
      font-family: "Montserrat";
    }
  }
}

.discount-code-style {
  .card-title-discount-code {
    display: unset;
  }
  .card-title {
    display: unset !important;
  }
  .discount {
    transform: translateY(-14px);
  }
}

.store-code-style {
  .header-card {
    .card-title {
      p {
        -webkit-line-clamp: 2;
      }
    }
    .card-icon {
      height: auto;
    }
  }
}

.voucher-store-promotion-card {
  margin: 40px;

  font-style: normal;
  max-width: 480px;
  min-height: 190px;
  padding: 16px 32px;
  gap: 12px;
  background: $primary-color;
  position: relative;
  .modal-card-promotion {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 12px;
    gap: 24px;
    max-width: 420px;
    min-height: 158px;
    background: $white-color;
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.2);
    border-radius: 12px;
    .ant-card-body {
      padding: 0px;
      .header-card {
        display: flex;
        margin-bottom: 10px;
        .discount-icon {
          svg {
            width: 34px;
            height: 34px;
          }
        }
        .title-text {
          max-width: 330px;
          font-weight: 600;
          font-size: 20px;
          line-height: 24px;
          color: #282828;
          padding: 0px 12px;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        .applied-icon {
          position: absolute;
          float: right;
          right: 15px;
        }
      }

      .content-card {
        font-style: normal;
        color: #282828;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 8px;
      }

      .dahsed-line {
        width: 100%;
        max-width: 340px;
        height: 1px;
        border: 1px dashed #c1c1c1;
        position: relative;
      }

      .footer-card {
        display: flex;
        height: 40px;
        align-items: flex-end;
        .date-time {
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          margin-left: 8px;
          color: #282828;
        }
        .clock-icon {
          display: flex;
        }
      }
    }
  }
  .left-half-circle {
    float: left;
    position: absolute;
    z-index: 2;
    transform: translate(-32px, -15px);
  }

  .right-half-circle {
    width: 12px;
    position: absolute;
    float: left;
    right: 0px;
    transform: translate(0px, -14px);
    li {
      &::after {
        border-radius: 12px 0px 0px 12px;
      }
    }
  }
  ul li {
    margin: 10px 0px;
    &::after {
      content: "";
      display: inline-block;
      position: absolute;
      width: 12px;
      height: 20px;
      -moz-border-radius: 7.5px;
      -webkit-border-radius: 7.5px;
      border-radius: 0px 12px 12px 0px;
      background-color: $white-color;
    }
    &::marker {
      display: none;
      content: "";
    }
  }
}

.voucher-promotion-card {
  background: #ffc4b6;
  .header-card {
    .title-text {
      padding-left: 0px !important;
    }
  }
  .content-card {
    display: flex;
    justify-content: space-between;
    .left-content {
      display: flex;
      flex-direction: column;
      height: 60px;
      justify-content: space-between;
      .discount-code {
        display: flex;
        padding: 4px 8px;
        gap: 8px;
        max-width: 241px;
        height: 24px;
        background: #fdf6f4;
        border-radius: 12px;
        .code {
          font-style: normal;
          font-weight: 700;
          font-size: 14px;
          line-height: 16px;
          display: flex;
          align-items: center;
          letter-spacing: 0.3px;

          color: $primary-color;
        }
      }
      .text-content {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: #282828;
        max-width: 300px;
      }
    }
    .btn-redeem {
      width: 100%;
      max-width: 84px;
      max-height: 40px;
      background: $primary-color;
      border: 1px solid $primary-color;
      border-radius: 50px;
      float: right;
      position: absolute;
      right: 10px;

      button {
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        display: flex;
        align-items: center;
        color: $white-color;
        outline: none;
        border: none;
        background: none;
        line-height: 40px;
        margin: 0 auto;
        cursor: pointer;
      }
    }
    .btn-remove {
      width: 100%;
      max-width: 84px;
      max-height: 40px;
      background: $white-color;
      border: 1px solid $primary-color;
      border-radius: 50px;
      float: right;
      position: absolute;
      right: 10px;

      button {
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        display: flex;
        align-items: center;
        color: $primary-color;
        outline: none;
        border: none;
        background: none;
        line-height: 40px;
        margin: 0 auto;
        cursor: pointer;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .voucher-promotion-card {
    .modal-card-promotion {
      .ant-card-body {
        .content-card {
          .btn-redeem {
            padding: 5.64361px 8.46541px;
            gap: 5.64px;
            width: 59.26px;
            height: 28.29px;
            border: 0.705451px solid $primary-color;
            border-radius: 35.2725px;
            top: 44%;
            button {
              font-size: 9.87631px;
              line-height: 17px;
              display: flex;
              align-items: center;
              color: $white-color;
            }
          }
        }
      }
    }
  }

  .card-list-container{
    height: 140px;
    .card-list{
      .fnb-promotion-card {
        min-width: 257px;
        min-height: 110px;
        width: 237px;
        height: 110px;
        padding-right: 20px;
        .header-card {
          .card-title {
            top: 3px;
            font-size: 10.6024px;
            line-height: 16px;
            padding: 8px 10px 0px 12px;
          }
          .card-icon {
            position: relative;
            top: 4px;
            left: -1px;
            .card-icon-content {
              padding: 7px 0px;
              border: none;
              .btn-redeem {
                top: 2px;
                max-width: 55px;
                max-height: 26px;
                font-size: 9px;
              }
            }
          }
          .card-icon:before {
            content: "";
            display: block;
            width: 100%;
            height: 180%;
            position: absolute;
            border-radius: 18px;
          }
        }
    
        .discount-code {
          top: 2px;
          height: 13px;
          .discount {
            max-width: 155px;
            max-height: 16px;
            border-radius: 7.95181px;
            .icon {
              width: 10px;
              height: 10px;
            }
            .code {
              font-size: 9px;
              line-height: 11px;
            }
          }
        }
    
        .content-card {
          font-size: 8px;
          line-height: 13px;
          height: 70px;
          ul li {
            font-weight: 400;
            font-size: 9px;
            line-height: 20px;
            font-family: "Montserrat";
          }
          p {
            padding-top: $border-radius;
            transform: translateX(-12px);
            font-weight: 400;
            font-size: 8px;
            line-height: 20px;
            letter-spacing: 0.3px;
          }
          .discount-amount{
            padding: 2px 12px 0px 12px;
          }
          .end-date{
            padding: 0px;
            position: relative;
            top: -15px;
          }
        }
    
        .content-card-discount {
          font-size: 8px;
          line-height: 13px;
          height: 75px;
          padding: 3px 12px 0px 25px;
          .end-date{
            padding: 0px;
            position: relative;
            top: -15px;
          }
        }
      }
    }
  }
}
