.flash-sale-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 0;
}
.flash-sale-home-page {
  $ant-tabs-content-holder-padding-top-bottom: 32px;
  $ant-tabs-content-holder-padding-left-right: 20px;
  $crossbar-count-down-height: 76px;
  $ant-tabs-nav-height: 307px;

  margin-top: 0px;
  margin-bottom: 0px;
  position: relative;
  .z-index-3 {
    z-index: 3 !important;
  }
  .ant-tabs-tabpane {
    /* fix conflicts in theme customization */
    padding-left: 0px;
    padding-right: 0px;
  }
  .crossbar-count-down {
    position: absolute;
    z-index: 2;
    height: $crossbar-count-down-height;
    background-color: #000;
    width: 100%;
    left: 0;
    top: $ant-tabs-nav-height;
    padding: 12px 16px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    .crossbar-count-down-title {
      font-style: normal;
      font-weight: 700;
      font-size: 20px;
      line-height: 28px;
      color: #ffffff;
      margin-right: 24px;
    }
    .flash-sale-banner-zero-custom {
      --fcc-digit-block-width: 25px !important;
      --fcc-digit-block-height: 43px !important;
      --fcc-digit-font-size: 24px !important;
      --fcc-separator-size: 4px !important;
      ._3cpN7 {
        ._2EBbg:first-child {
          margin-right: 4px;
        }
        ._2EBbg {
          div:first-child {
            border-top-left-radius: 4px;
            border-top-right-radius: 4px;
          }
          div:nth-child(2) {
            border-bottom-left-radius: 4px;
            border-bottom-right-radius: 4px;
          }
        }
      }
      ._2hRXr {
        margin-left: 8px;
        margin-right: 8px;
      }
    }
  }
  .ant-tabs {
    border-radius: 15px;
    box-shadow: 0px 4px 9px rgba(0, 0, 0, 0.25);
    .ant-tabs-nav {
      padding-bottom: 18px;
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
      margin-bottom: 0px;
      height: $ant-tabs-nav-height;
      background-image: url("../../../../assets/images/background-flash-sale.png");
      background-repeat: no-repeat;
      object-fit: cover;
      background-size: cover;
      background-position: center;
      .ant-tabs-nav-list {
        width: 100%;
        justify-content: space-evenly;
        .ant-tabs-tab,
        .ant-tabs-tab-active {
          align-items: flex-end;
          color: #fff;
          padding: 8px 0;
          .ant-tabs-tab-btn {
            width: 120px;
            color: #fff;
            text-align: center;

            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 30px;
            letter-spacing: 0.3px;
            text-shadow: 0 0 0.25px currentcolor;
          }
        }
        .ant-tabs-tab-active .tab-item-time {
          font-weight: 700;
        }
        .ant-tabs-ink-bar {
          background: #fff;
          width: 100%;
        }
        .tab-item-name {
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 24px;
          text-align: center;
          color: #ffffff;
          order: 1;
          align-self: stretch;
          flex-grow: 0;
        }
        .tab-item-time {
          font-style: normal;
          font-weight: 500;
          font-size: 32px;
          line-height: 44px;
          display: flex;
          align-items: center;
          justify-content: center;
          text-align: center;
          color: #ffffff;
          .tab-item-time-separator {
            font-size: 24px;
            line-height: 32px;
            padding: 0 12px;
          }
        }
      }
    }
    .ant-tabs-ink-bar.ant-tabs-ink-bar-animated {
      height: 3px;
      border-radius: 3px;
    }
    .ant-tabs-content-holder {
      padding: $ant-tabs-content-holder-padding-top-bottom $ant-tabs-content-holder-padding-left-right;
      padding-top: calc($ant-tabs-content-holder-padding-top-bottom + $crossbar-count-down-height);
      background-color: #fff;
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
    }
  }
  .flip-countdown-title {
    display: none;
  }
}

/* Mobile - ant-xs */
@media (max-width: 575px) {
  .flash-sale-container {
    padding: 0;
  }
  .flash-sale-home-page {
    $flash-sale-home-page-padding: 16px;
    $ant-tabs-content-holder-padding-top-bottom: 32px;
    $ant-tabs-content-holder-padding-left-right: 20px;
    $crossbar-count-down-height: 64px;
    $ant-tabs-nav-height: 210px;

    margin-top: 0px;
    margin-bottom: 0px;
    padding: $flash-sale-home-page-padding;

    .crossbar-count-down {
      position: absolute;
      z-index: 2;
      height: $crossbar-count-down-height;
      background-color: #000;
      width: calc(100% - $flash-sale-home-page-padding * 2);
      left: $flash-sale-home-page-padding;
      top: $ant-tabs-nav-height;
      padding: 12px 16px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .crossbar-count-down-title {
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        color: #ffffff;
        margin-right: 0px;
      }
    }
    .ant-tabs {
      border-radius: 8px;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      .ant-tabs-nav {
        padding-bottom: 16px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        margin-bottom: 0px;
        height: $ant-tabs-nav-height;
        background-image: url("../../../../assets/images/background-flash-sale-mobile.png");
        background-repeat: no-repeat;
        object-fit: cover;
        background-size: cover;
        background-position: top;
        .ant-tabs-nav-wrap {
          margin-top: 140px;
          height: 54px;
        }
        .ant-tabs-nav-list {
          width: 100%;
          justify-content: space-evenly;
          text-align: center;
          .ant-tabs-tab,
          .ant-tabs-tab-active {
            align-items: flex-end;
            color: #fff;
            padding: 8px 0;
            margin: 0px;
            .ant-tabs-tab-btn {
              width: auto;
              color: #fff;
              text-align: center;

              font-style: normal;
              font-weight: 500;
              font-size: 24px;
              line-height: 30px;
              letter-spacing: 0.3px;
            }
          }
          .ant-tabs-ink-bar {
            background: #fff;
            width: 100%;
          }
          .tab-item-name {
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
          }
          .tab-item-time {
            font-weight: 500;
            font-size: 20px;
            line-height: 28px;
            display: flex;
            align-items: center;
            text-align: center;
            letter-spacing: 0.3px;
            color: #ffffff;
            .tab-item-time-separator {
              font-size: 24px;
              line-height: 32px;
              padding: 0 12px;
            }
          }
        }
      }
      .ant-tabs-content-holder {
        padding: $ant-tabs-content-holder-padding-top-bottom $ant-tabs-content-holder-padding-left-right;
        padding-top: calc($ant-tabs-content-holder-padding-top-bottom + $crossbar-count-down-height - 16px);
        background-color: #fff;
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
      }
    }
  }
}
