$LINE_HEIGHT_24: 24;
$FONT_WEIGHT_400: 400;
$WHITE_COLOR: "#FFF";
.post-content-theme2 {
  width: 784px;
  height: auto;
  margin-bottom: 60px;

  .post-content {
    flex-direction: column;
    background-color: #fff;
    border-radius: 20px;
    .thumbnail {
      .thumbnail-url {
        width: 784px;
        max-height: 466px;
        border-top-right-radius: 20px;
        border-top-left-radius: 20px;
        background-color: $WHITE_COLOR;
        cursor: pointer;
      }
    }
    .title {
      color: #db4d29;
      font-size: 16px;
      font-style: normal;
      font-weight: 600;
      line-height: 20px;
      text-transform: uppercase;
      margin-top: 16px;
      padding: 0px 24px;

      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      line-clamp: 2;
      -webkit-box-orient: vertical;
    }
    .label {
      font-size: 20px;
      font-style: normal;
      font-weight: 700;
      line-height: 24px;
      padding: 0px 24px;
      cursor: pointer;
      margin: 8px 0px;
      .label-text {
        color: #282828;
        font-size: 20px;
        overflow: hidden;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        text-overflow: ellipsis;
        max-width: 768px;
      }
    }
    .date-and-author {
      display: flex;
      align-items: center;
      padding: 0px 24px;

      .date {
        display: flex;
        color: #959595;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .author {
        margin-left: 24px;
        .text-by {
          color: #959595;
        }
      }
    }
    .description {
      color: #282828;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 24px;
      margin-top: 16px;
      padding: 0px 24px 24px 24px;

      .blog-list-page-content,
      .content-blog-theme2 {
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        line-clamp: 4;
        -webkit-box-orient: vertical;
      }
    }
  }
}

@media (max-width: 576px) {
  .post-content-theme2 {
    .post-content {
      .thumbnail {
        margin: 0 auto;
        .thumbnail-url {
          width: 360px;
          max-height: 240px;
        }
      }
    }
  }
}
