#themeRelatedProductsCheckout {
  scroll-margin-top: 70px;
}
.check_out_theme2_related_products {
  padding-top: 10px;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  .header_title {
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 44px;
    text-align: center;
    color: #db4d29;
    margin: auto !important;
    cursor: pointer;
  }
  .product_list {
    .product_row {
      display: flex;
      flex-flow: row wrap;
      justify-content: flex-start;
      gap: 20px;
      padding: 30px;
    }
  }
}

@media (max-width: 639px) {
  .check_out_theme2_related_products {
    .product_list {
      width: 100%;
      .product_row {
        display: flex;
        .swiper-related-product {
          .swiper-wrapper {
            .swiper-slide-related-product {
              width: fit-content;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 575px) {
  .product_list {
    overflow: scroll;
    max-width: 375px;
    flex-flow: unset !important;
  }
}
