.my-profile-info {
  padding-bottom: 20px !important;
}

.profile-page-background {
  font-style: normal;
  font-weight: 600;
  .profile-page-wrapper {
    display: flex;
    padding-top: 72px;
    padding-bottom: 32px;
    .web-d-none {
      display: none;
    }
    .profile-left-side {
      width: 392px;
      .profile-membership {
        background: #fecc2f;
        width: 111px;
        height: 26.42px;
        border-radius: 26.4183px;
        font-size: 12px;
        line-height: 26.42px;
        color: #ffffff;
        text-align: center;
      }
      .profile-contain {
        height: 120px;
        display: flex;
        align-items: center;
        gap: 10px;
        margin-bottom: 25px;
        margin-top: 8px;
        .profile-image {
          height: 120px;
          width: 120px;
          position: relative;
          .profile-img {
            border-radius: 60px;
          }
          .upload-profile-img {
            position: absolute;
            right: 0;
            bottom: 0;
            cursor: pointer;
          }
        }
        .profile-name {
          font-size: 20px;
          line-height: 21px;
          color: #000000;
          width: calc(100% - 130px);
        }
      }
      .profile-infor-contain {
        display: flex;
        gap: 32px;
        flex-direction: column;
        .profile-infor {
          font-size: 16px;
          line-height: 21px;
          color: #000000;
          margin-left: 15px;
          display: flex;
          gap: 15px;
          cursor: pointer;
        }
        .profile-infor-active {
          color: #db4d29;
        }
        .none-text-decoration {
          text-decoration: none !important;
        }
      }
    }
    .profile-right-side {
      min-height: 100%;
      width: 808px;
      background: #fff;
      border-radius: 8px;
      max-height: max-content;
      .my-profile-info {
        padding-bottom: 16px;
      }
      .profile-card-title {
        font-weight: 600;
        font-size: 24px;
        line-height: 21px;
        color: #000000;
        padding: 16px;
        border-bottom: 1px solid #f4f4f4;
        margin-bottom: 36px;
      }
      .profile-card-row {
        display: flex;
        align-items: center;
        margin-left: 28px;
        .profile-card-label {
          font-weight: 500;
          font-size: 16px;
          line-height: 20px;
          color: #000000;
          margin-bottom: 24px;
          width: 144px;
          display: flex;
          justify-content: end;
          margin-right: 24px;
        }
        .profile-card-input {
          width: 444.69px;
          height: 56px;
          border-radius: 12px;
          .ant-input {
            height: 54px !important;
            margin-top: -4px;
          }
        }
      }
      .exist-phone-code {
        color: #ff4d4f;
        margin-left: 200px;
        margin-bottom: 10px;
        margin-top: -15px;
      }
      .btn-update {
        width: 135px;
        height: 48px;
        background: #db4d29;
        border-radius: 12px;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        color: #fef7ed;
        border: 1px solid #db4d29;
        margin-left: 196px;
        cursor: pointer;
      }
      .profile-date-time-picker {
        width: 444.69px;
      }
    }
  }

  /* Mobile */
  @media (max-width: 740px) {
    .page-container {
      margin: 0 16px;
    }
    .profile-page-wrapper {
      flex-direction: column;
      .mobile-d-none {
        display: none;
      }
      .web-d-none {
        display: block;
      }
      .navigate-profile-page {
        display: flex;
        gap: 12px;
        font-size: 20px;
        line-height: 21px;
        color: #000000;
        margin-bottom: 16px;
      }
      .profile-loyalty-point-hide {
        display: none;
      }
      .profile-left-side {
        height: 100%;
        width: 100%;
        .btn-logout {
          height: 48px;
          background: #db4d29;
          border-radius: 12px;
          font-weight: 700;
          font-size: 16px;
          line-height: 20px;
          color: #fef7ed;
          border: none;
        }
      }
      .profile-right-side {
        height: 100%;
        width: 100%;
        .my-profile-info {
          margin: 28px 0px;
        }
        .profile-card-row {
          flex-direction: column;
          align-items: unset !important;
          margin: 0 12px !important;
          .profile-card-label {
            display: block;
            margin-bottom: 8px;
          }
          .profile-card-input {
            width: 100%;
          }
        }
        .profile-date-time-picker {
          width: 100%;
        }
        .profile-card-title {
          display: none;
        }
        .btn-update {
          display: block;
          margin: auto;
          margin-top: 12px;
        }
      }
    }
  }
}

.fnb-date-time-picker {
  width: 100%;
  height: 60px;
  border-radius: 16px;
  box-shadow: 0px 1px 4px rgb(0 0 0 / 25%);

  .ant-picker-input {
    flex-direction: row-reverse;
    input {
      font-size: 14px;
      line-height: 17px;
      padding: 12px 16px;
    }
  }
}

.fnb-date-time-picker-dropdown {
  font-size: 18px;
  border-radius: 16px;

  .ant-picker-cell-selected {
    width: 35px !important;
    height: 35px !important;
    border-radius: 50%;
    background-color: #db4d29;
    .ant-picker-cell-inner {
      background: #db4d29 !important;
    }
  }

  .ant-picker-cell-inner {
    width: 25px;
    border-color: #db4d29;
    text-align: center;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  .ant-picker-panel-container {
    border-radius: 16px;
    .ant-picker-panel {
      .ant-picker-footer {
        display: none;
        .ant-picker-ranges {
          .ant-picker-ok {
            button.ant-btn.ant-btn-primary.ant-btn-sm {
              min-width: 80px !important;
              height: 40px !important;
            }
          }
        }
      }

      .ant-picker-datetime-panel {
        .ant-picker-date-panel {
          .ant-picker-header {
            background: #ffffff !important;
            font-size: 18px;
            border-bottom: unset;
            .ant-picker-header-view {
              .ant-picker-month-btn {
                color: #db4d29 !important;
              }
            }
            padding: 0 30px !important;
          }

          .ant-picker-header * {
            color: #000 !important;
            font-weight: bold;
          }

          .ant-picker-header button:hover {
            font-weight: bold !important;
          }
        }

        .ant-picker-time-panel {
          .ant-picker-header {
            background: #ffffff !important;
            font-size: 18px;
            border-bottom: unset;
            padding: 0 30px !important;
          }

          .ant-picker-header * {
            color: #000 !important;
            font-weight: bold;
          }
        }
      }
    }
  }
}

.profile-page-background .ant-radio-checked .ant-radio-inner {
  border-color: #db4d29 !important ;
  background-color: #db4d29 !important ;
}
.ant-input-affix-wrapper:hover {
  border-color: unset !important ;
}

.profile-page-background .ant-radio-checked .ant-radio-inner:after {
  background-color: white !important ;
}

.profile-page-background .ant-radio:hover .ant-radio-inner {
  border-color: #db4d29 !important ;
}

@media (max-width: 992px) and (min-width: 576px) {
   .page-container-loyalty-point {
    .profile-page-wrapper {
      .profile-left-side {
        display: none;
      }
    }
   }
}
