$margin_top: 30px;
$padding_login: 0px;
$number_width: 60px;
$number_height: 60px;
$margin_otp_left: 17px;
$number_width_mobile: 52px;
$number_height_mobile: 52px;
$margin_otp_left_mobile: 9.89px;
.login-page-container-theme2 {
  padding: 60px 32px;

  .login_page_theme2 {
    margin: auto;
    width: 444px;
    height: fit-content;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 20px;
    align-items: center;
    text-align: center;
    position: relative;

    .loading-overlay {
      position: absolute;
      z-index: 1000;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: rgba(255, 255, 255, 0.7);

      .loading {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1000;
      }
    }

    .login_title {
      padding-left: $padding_login;
      padding-right: $padding_login;

      font-style: normal;
      font-weight: 700;
      font-size: 32px;
      line-height: 44px;
      align-items: center;
      text-align: center;
    }

    .login_detail {
      margin-top: $margin_top;
      .login_phone_icon {
        position: relative;
        padding-left: 18px;
        padding-right: 5px;
        top: 47px;
        z-index: 2;
        float: left;
      }

      .login_country {
        position: relative;

        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        color: #000000;
        z-index: 2;

        .login_country_popup {
          width: 600px;
        }

        float: left;
        top: 44px;
      }

      .login_phone {
        padding-inline-start: 100px;

        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        color: #000000;
        float: left;
        border-radius: 12px;
        display: flex;
        align-items: center;
        height: 56px;

        .ant-input {
          margin-left: 15px;
        }
      }
    }

    .login_phone_error {
      padding-left: $padding_login;

      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 23px;
      float: left;
      color: #ff0000;
    }

    .login_firebase_error {
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 23px;
      float: left;
      color: #ff0000;
    }

    .login_button_box {
      margin-top: $margin_top;
      .login_button {
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        width: 100%;
        opacity: 1;
        border-radius: 12px;
        color: white;
      }
    }

    .login_otp_phone {
      margin: auto;
      text-align: center;

      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 23px;
      align-items: center;
      text-align: center;
    }

    .login_otp_title1 {
      margin: auto;
      text-align: center;

      font-style: normal;
      font-weight: 700;
      font-size: 40px;
      line-height: 44px;
      margin-top: 160px;
    }

    .login_otp_title2 {
      margin: auto;
      text-align: center;

      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      align-items: center;
      color: #282828;
      margin-top: 28px;
    }

    .login_otp_title3 {
      margin: auto;
      text-align: center;

      font-style: normal;
      font-weight: 700;
      font-size: 14px;
      line-height: 17px;
      margin-top: 12px;
    }

    .login_otp_resend_title {
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 23px;
      align-items: center;
      text-align: center;
      color: #000000;
      margin-top: 25px;
    }

    .login_otp_resend1 {
      display: none;
      cursor: pointer;
      margin: auto;
      text-align: center;
      color: rgba(94, 141, 236, 1);
    }

    .login_otp_resend2 {
      cursor: pointer;
      margin: auto;
      text-align: center;
      color: rgba(190, 190, 190, 1);
    }

    .login_otp_numbers {
      margin: auto;
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      margin-top: 20px;
      width: fit-content;

      .login_otp_number1 {
        height: $number_height;
        width: $number_width;

        .login_otp_n1 {
          text-align: center;
          background: #fef7ed;
          border: 1px solid #6d6d6d;
          border-radius: 12px;
          width: 100%;
          height: 100%;

          font-style: normal;
          font-weight: 700;
          font-size: 18px;
          line-height: 23px;
          align-items: center;
          text-align: center;
        }
      }

      .login_otp_number2 {
        height: $number_height;
        width: $number_width;
        margin-left: $margin_otp_left;

        .login_otp_n2 {
          text-align: center;
          text-align: center;
          background: #fef7ed;
          border: 1px solid #6d6d6d;
          border-radius: 12px;
          width: 100%;
          height: 100%;

          font-style: normal;
          font-weight: 700;
          font-size: 18px;
          line-height: 23px;
          align-items: center;
          text-align: center;
        }
      }

      .login_otp_number3 {
        height: $number_height;
        width: $number_width;
        margin-left: $margin_otp_left;

        .login_otp_n3 {
          text-align: center;
          text-align: center;
          background: #fef7ed;
          border: 1px solid #6d6d6d;
          border-radius: 12px;
          width: 100%;
          height: 100%;

          font-style: normal;
          font-weight: 700;
          font-size: 18px;
          line-height: 23px;
          align-items: center;
          text-align: center;
        }
      }

      .login_otp_number4 {
        height: $number_height;
        width: $number_width;
        margin-left: $margin_otp_left;

        .login_otp_n4 {
          text-align: center;
          text-align: center;
          background: #fef7ed;
          border: 1px solid #6d6d6d;
          border-radius: 12px;
          width: 100%;
          height: 100%;

          font-style: normal;
          font-weight: 700;
          font-size: 18px;
          line-height: 23px;
          align-items: center;
          text-align: center;
        }
      }

      .login_otp_number5 {
        height: $number_height;
        width: $number_width;
        margin-left: $margin_otp_left;

        .login_otp_n5 {
          text-align: center;
          text-align: center;
          background: #fef7ed;
          border: 1px solid #6d6d6d;
          border-radius: 12px;
          width: 100%;
          height: 100%;

          font-style: normal;
          font-weight: 700;
          font-size: 18px;
          line-height: 23px;
          align-items: center;
          text-align: center;
        }
      }

      .login_otp_number6 {
        height: $number_height;
        width: $number_width;
        margin-left: $margin_otp_left;

        .login_otp_n6 {
          text-align: center;
          text-align: center;
          background: #fef7ed;
          border: 1px solid #6d6d6d;
          border-radius: 12px;
          width: 100%;
          height: 100%;

          font-style: normal;
          font-weight: 700;
          font-size: 18px;
          line-height: 23px;
          align-items: center;
          text-align: center;
        }
      }
    }

    .login_otp_error {
      color: red;

      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 23px;
      align-items: center;
      text-align: center;
      margin-top: 25px;
    }

    .login_button_resend_box {
      width: calc(6 * $number_width + 5 * $margin_otp_left + 5px);
      margin: auto;

      .login_button_resend {
        margin-top: 10px;
        width: 100%;
        background: #db4d29;
        border-radius: 12px;

        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        align-items: center;
        text-align: center;
        color: #fef7ed;
      }
    }

    .login_otp_footer {
      height: 209px;
    }
  }
}

@media screen and (max-width: 639px) {
  .login-page-container-theme2 {
    padding: 24px 16px !important;

    .login_page_theme2 {
      width: 100% !important;
      .login_title {
        font-size: 24px;
        line-height: 17px;
      }

      .login_detail {
        .login_country,
        .login_phone {
          font-size: 14px !important;
        }
      }

      .login_otp_title1 {
        margin-top: 60px;
      }

      .login_otp_numbers {
        .login_otp_number1 {
          height: $number_height_mobile;
          width: $number_width_mobile;
        }

        .login_otp_number2 {
          height: $number_height_mobile;
          width: $number_width_mobile;
          margin-left: $margin_otp_left_mobile;
        }

        .login_otp_number3 {
          height: $number_height_mobile;
          width: $number_width_mobile;
          margin-left: $margin_otp_left_mobile;
        }

        .login_otp_number4 {
          height: $number_height_mobile;
          width: $number_width_mobile;
          margin-left: $margin_otp_left_mobile;
        }

        .login_otp_number5 {
          height: $number_height_mobile;
          width: $number_width_mobile;
          margin-left: $margin_otp_left_mobile;
        }

        .login_otp_number6 {
          height: $number_height_mobile;
          width: $number_width_mobile;
          margin-left: $margin_otp_left_mobile;
        }
      }

      .login_button_box {
        .login_button {
          font-weight: 700 !important;
          font-size: 14px !important;
          line-height: 17px !important;
        }
      }

      .login_button_resend_box {
        width: calc(6 * $number_width_mobile + 5 * $margin_otp_left_mobile + 5px);
      }

      .login_otp_footer {
        height: 84px;
      }
    }
  }
}
