.card-delivery-info {
  width: 100%;
  .ant-card-body {
    padding: 0px;
  }
  .delivery-info {
    width: 100%;
    .left-content-delivery {
      width: 52px;
      display: flex;
      justify-content: center;
      padding-top: 4px;
    }
    .right-content-delivery {
      width: calc(100% - 52px);
    }
    .phone-info,
    .address-info,
    .user-info {
      display: flex;
      padding-top: 16px;
      padding-right: 8px;
    }
    .user-info {
      padding-bottom: 16px;
    }
    .title-text,
    .detail-text,
    .link-text {
      font-size: 14px;
      line-height: 20px;
    }
    .detail-text {
      padding-top: 4px;
      font-weight: 700;
    }
    .title-text,
    .link-text {
      font-weight: 400;
    }
    .link-text {
      padding-top: 8px;
      color: #1976d2;
      cursor: pointer;
    }
  }
}
