.profile-right-side-order-detail {
  background: transparent !important;
}
.confirm-modal-config {
  .confirmation-modal-theme2 .ant-modal-content {
    width: 378px !important;
    left: 0 !important;
  }

  .confirmation-modal-theme2 .ant-modal-content .ant-modal-body {
    padding: 24px 24px 24px 24px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #000000;
  }

  .confirmation-modal-theme2 .ant-modal-content .ant-modal-footer .ant-btn-default {
    min-width: -moz-fit-content;
    min-width: fit-content;
    background: #ffffff;
    width: 111px;
  }

  .confirmation-modal-theme2 .ant-modal-content .ant-modal-footer button {
    height: 48px;
    border-radius: 12px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    width: 211px;
  }
}
.order-detail-container {
  width: 100%;
  height: max-content;
  display: grid;
  row-gap: 24px;

  .order-detail-header {
    background: #ffffff;
    border-radius: 8px;
    .header-image {
      position: relative;

      .image {
        width: 100%;
        height: 332px;
      }

      .back-icon {
        position: absolute;
        left: 2%;
        top: 5%;
      }

      .back-icon:hover {
        cursor: pointer;

        path {
          font-weight: bold;
        }
      }

      .header-status {
        display: grid;
        vertical-align: middle;
        text-align: right;
        position: absolute;
        line-height: 2;
        margin-right: 16px;
        right: 0;
        top: calc(332px / 5);

        .order-detail-code {
          font-size: 20px;
          font-weight: 700;
          color: #282828;
        }

        .order-detail-status {
          font-size: 24px;
          font-weight: 700;
          color: #ff8c21;
        }

        .order-detail-created-platform {
          font-size: 16px;
          font-weight: bold;
          color: #282828;

          .order-detail-created-time {
            color: #959595;
          }
        }

        .receive-point-container {
          max-width: 383px;
          .receive-point-text {
            font-style: normal;
            border-radius: 10px;
            font-size: 14px;
            font-weight: 500;
            line-height: 20px;
            padding-top: 10px;
            display: flex;
            .point-logo {
              vertical-align: middle;
              margin-left: 12px;
            }
            .earn-points {
              color: #ffa318;
              font-weight: 500;
              line-height: 20px;
              font-size: 14px;
            }
          }
        }

        .group-btn {
          width: 100%;
          height: max-content;
          margin-top: 15px;
          .ant-btn.btn-status {
            width: 135px;
            border: 1px solid #6a6a6a;
            height: 48px;
            text-align: center;
            border-radius: 12px;
            background: transparent;
            font-size: 16px;
            font-weight: 500;
            color: #6a6a6a;
          }

          .ant-btn.btn-status-re-order {
            border: 1px solid #db4d29;
            color: #ffffff;
            background: #db4d29;
          }

          .ant-btn.btn-checkout {
            background: #fff;
            color: #db4d29;
            border: 1px solid #db4d29;
          }
        }
      }
    }

    .progress-bar {
      width: 100%;
      height: 271px;
      display: inline-flex;
      align-items: center;
      justify-content: space-evenly;

      .progress-bar-item {
        display: grid;
        gap: 12px;
        vertical-align: middle;
        text-align: center;
        width: max-content;

        .created-status-icon {
          width: 100%;
          text-align: center;
        }

        .status-describe {
          .created-time {
            display: grid;
            font-size: 16px;
            font-weight: 500;
            color: #888a98;
          }

          .created-status-name {
            font-size: 20px;
            font-weight: 600;
            color: #000000;
          }

          .status-schedule {
            color: #888a98;
          }
        }
      }

      .straight {
        width: 110px;
        height: 4px;
        background: #db4d29;
      }

      .straight-schedule {
        background: #888a98;
      }
    }
  }

  .order-detail-information {
    width: 100%;
    height: max-content;
    background: #ffffff;
    border-radius: 8px;

    .title {
      width: 100%;
      height: 68px;
      display: flex;
      align-items: center;
      background: #f9f9f9;
      font-size: 24px;
      font-weight: 600;
      color: #282828;

      .straight {
        width: 16px;
      }
    }

    .receiver-information {
      width: 100%;
      display: flex;
      align-items: center;
      padding: 24px 0 40px 0;

      .left-contain {
        width: 50%;
        padding: 0 16px;

        .group-item {
          display: grid;
          text-align: left;
          vertical-align: middle;
          row-gap: 10px;

          .label {
            font-size: 16px;
            font-weight: 500;
            color: #959595;
          }

          .value {
            font-size: 20px;
            font-weight: 600;
            color: #282828;
          }
        }
      }

      .right-contain {
        width: 50%;
        padding: 0 16px;

        .group-item {
          display: grid;
          text-align: left;
          vertical-align: middle;
          row-gap: 10px;

          .label {
            font-size: 16px;
            font-weight: 500;
            color: #959595;
          }

          .value {
            font-size: 20px;
            font-weight: 600;
            color: #282828;
          }
        }
      }
    }

    .receive-address {
      width: 100%;
      height: max-content;
      padding: 0 0 36px 0;

      .group {
        display: flex;
        align-items: center;
        padding: 0 23px;
        gap: 30px;

        .from-address {
          width: 45px;
          text-align: center;
          .straight {
            height: 23px;
            border: 1px dashed #db4d29;
            height: max-content;
            margin: 14px 0;
          }
        }

        .from-address-straight {
          padding: 5px 0;
        }

        .from-address-detail {
          display: grid;
          row-gap: 5px;

          .from-address-detail-title {
            color: #959595;
            font-size: 16px;
            font-weight: 500;
          }

          .from-address-detail-content {
            font-weight: 600;
            color: #282828;
            font-size: 20px;
          }
        }
      }
    }
  }

  .order-detail-product-list {
    width: 100%;
    height: max-content;
    background: #ffffff;
    border-radius: 8px;

    .title {
      width: 100%;
      height: 68px;
      display: flex;
      align-items: center;
      background: #f9f9f9;
      font-size: 24px;
      font-weight: 700;
      color: #282828;
      line-height: 32px;

      .straight {
        width: 16px;
      }
    }

    .order-detail-product-list-content {
      width: 100%;
      height: max-content;
      background: #fff;

      .group {
        padding: 24px 16px;

        .product-item {
          display: grid;
          row-gap: 8px;
          .topping-group {
            padding: unset !important;
            .image {
              height: unset !important;
            }
          }

          .product-item-group {
            display: flex;
            align-items: start;
            padding: 12px 0;
            .image {
              width: 80px;
              height: 80px;
              margin-right: 24px;
            }

            .product-header-left {
              width: calc(100% / 2);
              display: flex;
              .image {
                width: 80px;
                height: 80px;
                border-radius: 12px;
              }

              .product-item-name {
                .ant-typography {
                  font-size: 16px;
                  font-weight: 600;
                  color: #282828;
                  line-height: 24px;
                  font-family: "Montserrat";

                  .product-item-option {
                    font-size: 16px;
                    font-weight: 500;
                    color: #959595;
                  }
                }
              }
            }

            .product-header-right {
              min-width: calc(100% / 2);
              display: flex;
              align-items: start;
              justify-content: space-between;
              max-width: 100vw;
              .product-item-quantity {
                width: 50%;
                text-align: center;
                font-size: 16px;
                color: #282828;
                font-weight: 600;
              }

              .product-item-price {
                text-align: right;
                width: 50%;
                display: grid;
                vertical-align: middle;
                font-size: 16px;
                font-weight: 600;
                color: #282828;

                .product-item-price-discount {
                  font-size: 14px;
                  font-weight: 500;
                  color: #959595;
                  display: flex;
                  align-items: center;
                  justify-content: end;

                  .price {
                    text-decoration: line-through;
                  }

                  .discount-percent-value {
                    background: #ffa318;
                    border-radius: 20px;
                    color: #fff;
                    font-size: 14px;
                    font-weight: 600;
                    min-width: 46px;
                    min-height: 24px;
                    text-align: center;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-left: 5px;
                    padding: 3px 10px;
                  }
                }
              }
            }

            .product-topping-name {
              width: calc((100% - 50%) - 80px);
              font-size: 14px;
              font-weight: 500;
              color: #959595;
              line-height: 20px;
            }

            .product-option {
              width: 100%;
              padding: 0 10px;
            }

            .product-topping-quantity {
              width: calc((100% - 50%) / 2);
              text-align: center;
              font-size: 16px;
              color: #959595;
              font-weight: 500;
              white-space: nowrap;
            }

            .product-topping-price {
              text-align: right;
              width: calc((100% - 50%) / 2);
              font-size: 16px;
              font-weight: 500;
              color: #959595;
              .topping-price-col {
                display: flex;
                flex-direction: column;
                .topping-original-price {
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 400;
                  font-size: 14px;
                  line-height: 16px;
                  text-decoration-line: line-through;
                  color: #959595;
                  display: flex;
                  justify-content: flex-end;
                }
              }
            }

            .product-name {
              color: #282828;
              font-size: 16px;
              font-weight: 500;
              line-height: 24px;
            }
          }

          .product-item-group-not-padding {
            padding: 12px 0 0;
          }
        }

        .item-note {
          display: flex;
          width: 100%;
          position: relative;
          align-items: center;
          padding-left: 13%;
          margin-top: 4px;
          .note-text {
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: #282828;
            margin-left: 8px;
          }
        }
      }

      .straight {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        .item {
          width: calc(100% - 32px);
          border: 1px solid #f3f3f3;
        }
      }
    }

    .footer {
      display: flex;
      align-items: center;
      justify-content: end;
      .image {
        width: 100%;
        height: 100%;
      }

      .total-order-container {
        display: grid;
        align-items: center;
        position: absolute;
        justify-content: end;
        row-gap: 8px;
        width: 434px;
        padding-right: 16px;

        .group {
          display: flex;
          width: 434px;
          .label,
          .value {
            width: 50%;
            text-align: right;
            font-size: 16px;
            font-weight: 500;
            color: #282828;

            .order-detail-arrow {
              margin-left: 10px;
              position: relative;
              top: -2px;
            }
          }

          .payment-method {
            display: grid;
            justify-content: end;
          }

          .label-total {
            font-size: 20px;
            font-weight: 700;
            color: #282828;
          }

          .value-total {
            font-size: 20px;
            font-weight: 700;
            color: #db4d29;
          }

          .value {
            .paid-status {
              display: flex;
              align-items: center;
              justify-content: center;
              border-radius: 28px;
              padding: 6px 8px;
            }
            .status-unpaid {
              color: #fecc2f;
              background: rgba(254, 204, 47, 0.1);
            }

            .status-paid {
              color: #4ead4e;
              background: rgba(78, 173, 78, 0.2);
            }
          }
        }
        .group-note {
          flex-direction: column;
          align-items: end;
          padding-left: 12px;
          .title-note {
            font-weight: 700;
          }
          .content-note {
            text-align: right;

            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: #282828;
            margin-top: 4px;
          }
        }
      }
    }
  }
}
//Responsive
@media screen and (max-width: 740px) {
  .order-detail-popover {
    padding-left: calc((100vw - 363px)/2) !important;
  }
}
.ant-popover.order-detail-popover {
  padding: unset;
  position: absolute;
  .order-detail-popover-content {
    width: 363px;
    display: grid;
    padding: 12px;

    .group-body {
      padding-top: 5px;
      .list-item {
        margin-left: 24px;
        font-size: 20px;
      }
      .discount-fee-text {
        display: flex;
        justify-content: space-between;
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 16px;
        align-items: center;

        .discount-text {
          text-transform: capitalize;
          max-width: 75%;
          .discount-loyalty-point {
            color: #ff8c21;
          }
        }

        .discount-value {
          white-space: nowrap;
        }
      }
    }

    .discount-content-label {
      display: flex;
      font-weight: 700;
      font-size: 16px;
      padding-bottom: 6px;
      border-bottom: 1px dashed #d1d1d1;
      height: 24px;
    }

    .discount-content-text {
      display: flex;
      justify-content: space-between;
      border-top: 0.650038px dashed #d1d1d1;
      padding-top: 4px;
      font-weight: 500;
      font-size: 18px;
      line-height: 23px;
    }

    .discount-fee-label {
      display: flex;
      justify-content: space-between;
      padding-bottom: 6px;
      border-bottom: 1px dashed #d1d1d1;
      font-size: 16px;
      line-height: 20px;
      font-weight: 700;
    }
  }
}

@media (max-width: 740px) {
  .profile-right-side-order-detail {
    padding: unset !important;
  }

  .order-detail-container {
    width: 100%;
    .order-detail-header {
      .header-image {
        width: 100%;

        .back-icon {
          left: 5%;
        }

        .image {
          width: 100%;
        }

        .header-status {
          .order-detail-code {
            font-size: 16px;
          }

          .order-detail-status {
            font-size: 20px;
          }

          .order-detail-created-platform {
            font-size: 14px;
          }
        }
      }
      .progress-bar {
        display: grid;
        align-items: center;
        height: max-content;
        row-gap: 10px;
        padding: 16px;
        justify-content: start;

        .progress-bar-item {
          display: flex;
          gap: 16px;

          .status-describe {
            display: grid;
            row-gap: 8px;
            white-space: nowrap;
            align-items: center;

            .created-status-name {
              font-size: 16px;
            }

            .created-time {
              font-size: 14px;
              text-align: left;
            }
          }

          .created-status-icon {
            svg {
              width: 60px;
              height: 60px;
            }
          }
        }

        .straight {
          width: 3px;
          height: 20px;
          background: #db4d29;
          margin-left: 14%;
          border-radius: 20px;
        }

        .straight-schedule {
          background: #888a98;
        }
      }
    }

    .order-detail-information {
      .receiver-information {
        display: grid;
        align-items: center;
        padding: 24px 0;
        row-gap: 10px;

        .left-contain,
        .right-contain {
          width: calc(100% - 32px);
        }
      }

      .receive-address {
        .group {
          .from-address-detail {
            .from-address-detail-content {
              font-size: 16px;
            }
          }

          .from-address {
            svg {
              width: 45px;
              height: 56px;
            }
          }
        }
      }
    }

    .order-detail-product-list {
      width: 100%;

      .order-detail-product-list-content {
        width: 100%;
        display: grid;

        .group {
          padding: 12px;
          padding-right: 16px;

          .product-item {
            .product-item-group {
              display: grid;

              .product-header-left {
                width: 100%;

                .image {
                  width: 60px;
                  height: 60px;
                  margin-right: 16px;
                }

                .product-item-name {
                  .ant-typography {
                    font-size: 16px;
                  }
                }
              }

              .product-header-right {
                .product-item-quantity {
                  width: 60px;
                  text-align: left;
                }

                .image {
                  width: 60px;
                  height: 0px;
                  margin: unset;
                }
              }

              .product-option {
                padding: unset;
              }
            }

            .topping-group {
              display: flex;

              .product-topping-name {
                width: calc((100% - 50%) - 60px);
                white-space: nowrap;
              }

              .product-option {
                width: calc(100% - 60px);
                white-space: unset;
              }

              .product-topping-quantity {
                width: calc((100% - 50%) / 2);
                text-align: right;
              }

              .product-topping-price {
                width: calc((100% - 50%) / 2);
              }

              .image {
                width: 60px;
                height: 0px;
                margin-right: 16px;
              }

              .product-name {
                margin-left: -2px;
              }
            }
          }
          .item-note {
            padding-left: 24%;
          }
        }
      }

      .footer {
        display: flex;
        align-items: self-start;
        justify-content: center;
        width: 100%;
        border-top: 1px solid #f3f3f3;
        .total-order-container {
          display: grid;
          align-items: center;
          position: absolute;
          justify-content: space-between;
          row-gap: 16px;
          width: calc(100% - 32px);
          padding-top: 16px;
          padding-right: unset;

          .group {
            width: calc(100vw - 36px);
            .label {
              text-align: left;
              margin-left: 12px;
            }

            .value {
              margin-right: 12px;
            }
          }
          .group-note {
            flex-direction: column;
            align-items: start;
            .content-note {
              text-align: left;

              font-style: normal;
              font-weight: 400;
              font-size: 14px;
              line-height: 20px;
              color: #282828;
            }
          }
        }
      }
    }
  }
}
