$PRIMARY_COLOR: #db4d29;
.blog-theme2 {
  .pagination-blog-theme2 {
    margin-bottom: 32px;
    .ant-pagination .ant-pagination-item-active {
      border-color: unset !important;
      border: none;
      color: $PRIMARY_COLOR;
      text-decoration: underline;
      background-color: unset !important;
      a {
        color: $PRIMARY_COLOR;
      }
    }
    .ant-pagination-prev,
    .ant-pagination-next {
      display: none;
    }
  }

  .no-post-content {
    // margin-right: 30%;
    .no-post {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 30vh;
      .text-no-post {
        color: #282828;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        margin-top: 16px;
      }
    }
  }
}

